import React, { useEffect } from 'react'
import SideInfo from '../SideInfo'
import "@fancyapps/ui/dist/fancybox.css";
import taichi from './images/1-taichi.png'
import accuracy from './images/2-accuracy.png'

export default function Ai() {

    useEffect(()=>{
        document.documentElement.scrollTop = 0

        const article = document.getElementById('article')
        const briefInfoHeight = document.getElementById('side_info')
        if(briefInfoHeight.clientHeight !== 0){
            article.style.height = briefInfoHeight.clientHeight + 'px'
        }
        window.onresize = ()=>{
            if(briefInfoHeight.clientHeight !== 0){
                article.style.height = briefInfoHeight.clientHeight + 'px'
            }else{
                article.style.height = 'auto'
            }
        }
    })

    return (
        <div className='ai'>
            <div className="container mt-3 mt-md-4 mt-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-9">
                        <div id="article" className="article color-grey">
                            <h2 className="title">
                                <a href="https://reurl.cc/k7zZWq" target="_blank" rel="noreferrer">Python+AI<span>link to colab</span></a>
                            </h2>
                            <div className="detail">
                                <ul>
                                    <li><p>:202111-12</p></li>
                                    <li><p>:</p></li>
                                    <li><p>:</p></li>
                                </ul>
                            </div>
                            <div id='content' className="content">
                                <h4><span></span><span></span><span></span><span>Keras</span><span>TensorFlow</span>API
                                </h4>
                                <h4>Multilayer Perceptron
                                </h4>
                                <h4>93.33%!
                                </h4>
                                <h4>
                                </h4>
                                <div className='image-area'>
                                    <div className="imgBox taichi">
                                        <div className="img_inner" data-fancybox="gallery1" data-src={taichi}>
                                            <div className="img" style={{backgroundImage: `url(${taichi})`}} ></div>
                                        </div>
                                    </div>
                                    <div className="imgBox">
                                        <div className="img_inner" data-fancybox="gallery2" data-src={accuracy}>
                                            <div className="img" style={{backgroundImage: `url(${accuracy})`}}></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <SideInfo/>
                </div>
            </div>
        </div>
    )
}
