import React, { useEffect } from 'react'
import SideInfo from '../SideInfo'
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import '@fancyapps/ui'
import "@fancyapps/ui/dist/fancybox.css";
import Overview from './images/overview.png'
import testForm1 from './images/testForm1.PNG' 
import testForm2 from './images/testForm2.PNG'
import Poster1 from './images/poster1.png'
import Poster2 from './images/poster2.png'


export default function ProjectManagement() {

    useEffect(()=>{
        document.documentElement.scrollTop = 0

        const article = document.getElementById('article')
        const briefInfoHeight = document.getElementById('side_info')
        if(briefInfoHeight.clientHeight !== 0){
            article.style.height = briefInfoHeight.clientHeight + 'px'
        }
        window.onresize = ()=>{
            if(briefInfoHeight.clientHeight !== 0){
                article.style.height = briefInfoHeight.clientHeight + 'px'
            }else{
                article.style.height = 'auto'
            }
        }
    })

    return (
        <div className='projectManagement'>
            <div className="container mt-3 mt-md-4 mt-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-9">
                        <div id="article" className="article color-grey">
                            <h2 className="title">
                                <a href="https://reurl.cc/k7zZWq" target="_blank" rel="noreferrer" style={{pointerEvents:'none'}}></a>
                            </h2>
                            <div className="detail">
                                <ul>
                                    <li><p>:20213-12</p></li>
                                    <li><p>:</p></li>
                                    <li><p>:</p></li>
                                </ul>
                            </div>
                            <div id='content' className="content">
                                <h4><span></span>
                                </h4>
                                <h4><span></span>
                                </h4>
                                <h4>
                                </h4>
                                <h4><span>Microsoft Project</span>Visual Basic ApplicationMicrosoft Excel
                                </h4>
                                <h4>
                                </h4>
                                <h3 className='mt-4'>1. </h3>
                                <h4><span></span>""""<span></span><span></span><span></span><span></span><span></span><span></span><span></span>
                                </h4>
                                <h5>#</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery1" data-src={Overview}>
                                        <div className="img" style={{backgroundImage: `url(${Overview})`}} />
                                    </div>
                                </div>
                                <h3 className='mt-4'>2. </h3>
                                <h4><span></span>
                                </h4>
                                <h4><span></span><span></span><span></span>
                                </h4>
                                <h5>#:</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery2" data-src={testForm1}>
                                        <div className="img" style={{backgroundImage: `url(${testForm1})`}} />
                                    </div>
                                </div>
                                <h4><span>Excel</span><span>PDF</span>
                                </h4>
                                <h5>#:</h5>
                                <div className="imgBox">
                                    <div className="img_inner" data-fancybox="gallery3" data-src={testForm2}>
                                        <div className="img" style={{backgroundImage: `url(${testForm2})`}} />
                                    </div>
                                </div>
                                <h3 className='mt-4'>3. </h3>
                                <h4></h4>
                                <Carousel className='myCarousel'>
                                    <div className="myCarouselObj" data-fancybox="gallery4" data-src={Poster1}>
                                        <img src={Poster1} alt=""/>
                                    </div>
                                    <div className="myCarouselObj" data-fancybox="gallery4" data-src={Poster2}>
                                        <img src={Poster2} alt=""/>
                                    </div>
                                </Carousel>
                            </div>
                        </div>
                    </div>
                    <SideInfo/>
                </div>
            </div>
        </div>
    )
}
