import React, { useEffect } from 'react'
import SideInfo from '../SideInfo'
import "@fancyapps/ui/dist/fancybox.css";

export default function Ai() {

    useEffect(()=>{
        document.documentElement.scrollTop = 0

        const article = document.getElementById('article')
        const briefInfoHeight = document.getElementById('side_info')
        if(briefInfoHeight.clientHeight !== 0){
            article.style.height = briefInfoHeight.clientHeight + 'px'
        }
        window.onresize = ()=>{
            if(briefInfoHeight.clientHeight !== 0){
                article.style.height = briefInfoHeight.clientHeight + 'px'
            }else{
                article.style.height = 'auto'
            }
        }
    })

    return (
        <div className='dataStructure'>
            <div className="container mt-3 mt-md-4 mt-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-9">
                        <div id="article" className="article color-grey">
                            <h2 className="title">
                                <a href="https://github.com/Xiaoxuan0117/data_structure_exercise" target="_blank" rel="noreferrer"><span>link to github</span></a>
                            </h2>
                            <div className="detail">
                                <ul>
                                    <li><p>:202010-12</p></li>
                                    <li><p>:</p></li>
                                    <li><p>:</p></li>
                                </ul>
                            </div>
                            <div id='content' className="content">
                                <h4>
                                </h4>
                                <h4><span>GitHub</span></h4>
                                <h3 className='mt-3'>
                                    1. 
                                </h3>
                                <h4>
                                    <span></span>
                                    <ul>
                                        <li>-</li>
                                        <li>-</li>
                                        <li>-</li>
                                    </ul>
                                </h4>
                                <h3 className='mt-4'>
                                    2. 
                                </h3>
                                <h4>
                                    <span></span><br/>
                                    
                                </h4>
                                <h3 className='mt-4'>
                                    3. (insertion sort)(merge sort)
                                </h3>
                                <h4>
                                    <span></span><br/>
                                    
                                </h4>
                                <h3 className='mt-4'>
                                    4. 
                                </h3>
                                <h4>
                                    <span></span><br/>
                                    
                                </h4>
                                <h3 className='mt-4'>
                                    5. (heap sort)(quick sort)
                                </h3>
                                <h4>
                                    <span></span><br/>
                                    
                                </h4>
                            </div>
                        </div>
                    </div>
                    <SideInfo/>
                </div>
            </div>
        </div>
    )
}
